@import url("https://fonts.googleapis.com/css?family=Lato:400,400i,700");


body{
  background:white;
  margin:0;
  padding:0;
  box-sizing:border-box;
  color:white;
  font-family: 'Lato', sans-serif;
}
.container{
  display:flex;
    align-items:center;
    justify-content:center;
    height:100vh;
    width:100%;
}
.header {
  min-height: 60vh;
  background: #009FFF;
background: linear-gradient(to right, #f8f7f6,#f8f7f6);
  color: rgb(3, 3, 3);
  clip-path: ellipse(100vw 60vh at 50% 50%);
  display: flex;
  align-items: center;
  justify-content: center;
}

.details {
  text-align: center;
}

.profile-pic {
  height: 5px;
  width: 5px;
  object-fit: center;
  border-radius: 50%;
  border: 2px solid rgb(230, 210, 31);
}

.location p {
  display: inline-block;
}

.location svg {
  vertical-align: middle;
}

.stats {
  display: flex;
}

.stats .col-4 {
  width: 10rem;
  text-align: center;
}

.heading {
  font-weight: 400;
  font-size: 1.3rem;
  margin: 1rem 0;
}


.btn-google {
  color: #545454;
  background-color: #ffffff;
  box-shadow: 0 1px 2px 1px #ddd
}

.or-container {
  align-items: center;
  color: #ccc;
  display: flex;
  margin: 25px 0
}

.line-separator {
  background-color: #ccc;
  flex-grow: 5;
  height: 1px
}

.or-label {
  flex-grow: 1;
  margin: 0 15px;
  text-align: center
}